<template>
  <div>
    <button type="button" @click="test" class="btn btn-sm btn-primary  ">test</button>
  </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
export default{
  data(){
    return{

    }
  },
  methods:{
    test(){
      let fd = new FormData()
      fd.append('data', `select * from sdd_laporan where id_user='2'`)//database setting di server
      fd.append('database','infolay3_test')
      axios.post('https://infolayanans.space/api/mysql/auto.php', fd
      , {
          headers: {
          Authorization: localStorage.getItem('auth._token.local')
          }
      }).then(res => {
          res.data.forEach(data=>{
            db.collection('users/2zqezMmcOyc27lEOg45AA5m7ntM2/laporan').doc().set(data,{merge:true}).then(res=>{
              console.log('berhasil')
            })
          })
          this.$forceUpdate()
      })
    }
  },
  mounted() {
    
  },
}
</script>